<template>
	<div>
		<Markdown>
			<h1>Card 卡片</h1>
			<p>通用卡片容器。</p>
			<h2>何时使用</h2>
			<p>基础容器，可以承载文字、列表、图片、段落，常用于后台概览页面。</p>
			<h2>代码演示</h2>
		</Markdown>
		<vui-row :gutter="20">
			<vui-col :span="12">
				<ExampleCardBasicUsage />
				<ExampleCardSimple />
				<ExampleCardBorderless />
				<ExampleCardShadow />
			</vui-col>
			<vui-col :span="12">
				<ExampleCardLoading />
				<ExampleCardGrid />
				<ExampleCardFlexibleContent />
			</vui-col>
		</vui-row>
		<Markdown>
			<h2 id="example-api">API</h2>
			<h3>Card 属性</h3>
			<table class="example-api-props">
				<thead>
					<tr>
						<th width="80">属性</th>
						<th>说明</th>
						<th width="150">类型</th>
						<th width="80">默认值</th>
					</tr>
				</thead>
				<tbody>
					<tr>
						<td>icon</td>
						<td>卡片图标类型，位于标题左侧</td>
						<td>String</td>
						<td>--</td>
					</tr>
					<tr>
						<td>title</td>
						<td>卡片标题</td>
						<td>String | Number</td>
						<td>--</td>
					</tr>
					<tr>
						<td>extra</td>
						<td>卡片附加内容，位于标题右侧</td>
						<td>String | Number</td>
						<td>--</td>
					</tr>
					<tr>
						<td>borderless</td>
						<td>是否隐藏外边框</td>
						<td>Boolean</td>
						<td><code>false</code></td>
					</tr>
					<tr>
						<td>padding</td>
						<td>卡片内部间距，数值类型时单位为 <code>px</code></td>
						<td>String | Number</td>
						<td><code>20</code></td>
					</tr>
					<tr>
						<td>shadow</td>
						<td>卡片阴影，可选值为 <code>never</code>、<code>always</code>、<code>hover</code> 或者不设</td>
						<td>String</td>
						<td><code>never</code></td>
					</tr>
					<tr>
						<td>loading</td>
						<td>当卡片内容还在加载中时，可以用 <code>loading</code> 展示一个占位</td>
						<td>Boolean</td>
						<td><code>false</code></td>
					</tr>
				</tbody>
			</table>
			<h3>Card 插槽</h3>
			<table class="example-api-props">
				<thead>
					<tr>
						<th width="80">名称</th>
						<th>说明</th>
					</tr>
				</thead>
				<tbody>
					<tr>
						<td>icon</td>
						<td>卡片图标，位于标题左侧</td>
					</tr>
					<tr>
						<td>title</td>
						<td>卡片标题</td>
					</tr>
					<tr>
						<td>extra</td>
						<td>卡片附加内容，位于标题右侧</td>
					</tr>
					<tr>
						<td>cover</td>
						<td>卡片封面，位于标题栏下方</td>
					</tr>
					<tr>
						<td>default</td>
						<td>卡片主体内容</td>
					</tr>
					<tr>
						<td>actions</td>
						<td>卡片操作组，位于卡片底部</td>
					</tr>
					<tr>
						<td>loading</td>
						<td>自定义加载状态下的显示内容</td>
					</tr>
				</tbody>
			</table>
			<h3>CardMeta 属性</h3>
			<table class="example-api-props">
				<thead>
					<tr>
						<th width="80">属性</th>
						<th>说明</th>
						<th width="150">类型</th>
						<th width="80">默认值</th>
					</tr>
				</thead>
				<tbody>
					<tr>
						<td>title</td>
						<td>标题内容</td>
						<td>String | Number</td>
						<td>--</td>
					</tr>
					<tr>
						<td>description</td>
						<td>描述内容</td>
						<td>String | Number</td>
						<td>--</td>
					</tr>
				</tbody>
			</table>
			<h3>CardMeta 插槽</h3>
			<table class="example-api-props">
				<thead>
					<tr>
						<th width="80">名称</th>
						<th>说明</th>
					</tr>
				</thead>
				<tbody>
					<tr>
						<td>avatar</td>
						<td>头像/图标</td>
					</tr>
					<tr>
						<td>title</td>
						<td>标题内容</td>
					</tr>
					<tr>
						<td>description</td>
						<td>描述内容</td>
					</tr>
				</tbody>
			</table>
		</Markdown>
	</div>
</template>

<script>
	import MixinCatalogue from "@/mixins/catalogue";
	import Markdown from "@/components/markdown";
	import ExampleCardBasicUsage from "./examples/basic-usage";
	import ExampleCardSimple from "./examples/simple";
	import ExampleCardBorderless from "./examples/borderless";
	import ExampleCardShadow from "./examples/shadow";
	import ExampleCardLoading from "./examples/loading";
	import ExampleCardGrid from "./examples/grid";
	import ExampleCardFlexibleContent from "./examples/flexible-content";

	export default {
		mixins: [
			MixinCatalogue
		],
		components: {
			Markdown,
			ExampleCardBasicUsage,
			ExampleCardSimple,
			ExampleCardBorderless,
			ExampleCardShadow,
			ExampleCardLoading,
			ExampleCardGrid,
			ExampleCardFlexibleContent
		}
	};
</script>


